@import '../../assets/scss/base';

.policy_sec {
  position: $relative;
  .sidebar_menu {
    overflow-y: $auto;
    overflow-x: $hidden;
    @media (max-width:767px) {
      width: 100%;
      margin: 0 0 10px;
    }
    @media (min-width:767.99px) {
      width: 320px;
      padding-right: 20px;
      min-height: calc(100vh - 70px);
      border-right: 1px solid $grey-color-700;
      position: $absolute;
      top: 0;
    }
    @media (min-width:991px) {
      width: 420px;
    }
    @media (max-width:767px) {
      max-height: 300px;
      margin-top: 10px;
    }
    ul {
      list-style: $none;
      padding: 0;
      margin: 0;
      li {
        border-bottom: 1px solid $grey-color-700;
        a {
          color: rgba($base-color, .5);
          @include font-size (1);
          padding: 1.3rem 0;
          line-height: 1.2;
          display: $block;
          &.active,
          &:hover {
            color: $base-color;
          }
        }
      }
    }
  }
  .sidebar_rht {
    padding-top: 20px;
    padding-bottom: 20px;
    @media (min-width:767.99px) {
      min-height: calc(100vh - 70px);
      padding-left: 350px;
    }
    @media (min-width:991px) {
      padding-left: 450px;
    }
    section {
      margin-bottom: 25px;
    }
  }
}