@import '../../assets/scss/base';

.team_sec_full {
    .title_h2 {
        margin-bottom: 30px;
        @media (min-width:1200px) {
            padding: 0 14%;
        }
        @media (max-width:1199px) {
            padding: 0 10%;
        }
    }
}

/** Vertical Scroll **/
.verticle_scroll {
    @include flexbox;
    overflow: $hidden;
    @media (min-width:1200px) {
        margin-right: calc(-50vw - -100%);
        height: calc(100vh - 200px);
    }
    figure {
        display: $inline-block;
        margin: 10px 0 0;
        width: 100%;
        img {
            @include border-radius (10px);
            max-width: 100%;
            width: 100%;
            vertical-align: middle;
            transition: opacity .2s;
            // &:hover {
            //     opacity: 1;
            // }
        }
    }
    // &:hover img {
    //     opacity: 0.5;
    // }
    .single-column {
        margin: 0 5px;
        animation: var(--animation, none) 16s infinite linear;
        &:hover {
            animation-play-state: paused;
        }
    }
}
.verticle_scroll .single-column:nth-of-type(odd) {
    align-self: flex-end;
    --direction: 50%;
}

